function newsSlider() {
    var swiper = new Swiper('.news-collection .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
    })
}

function sliderGraduates() {
    var swiper = new Swiper('.graduates .swiper', {
        slidesPerView: "auto",
        spaceBetween: 0,
    })
}

newsSlider()
sliderGraduates()
